.section {
    display: flex;
    flex-direction: column;
    justify-content: start;
    margin-bottom: 40px;    
}

.container {
    display: flex;
    flex-direction: column;
    padding: 0 16px;   
}

.list {    
    list-style-type: none;
    margin: 0;
    padding: 0;
    max-height: 70vh;
    min-height: 45vh;
    overflow-y: auto;    
}


.list::-webkit-scrollbar {
    width: 8px;
    
}

.list::-webkit-scrollbar-track {    
    background: #2F2F37;
}

.list::-webkit-scrollbar-thumb {
    background: #8585AD;
    height: 45vh;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.onHover {
    box-shadow: 0px 0px 10px #4c4cff;
}

.total {
    display: flex;
    justify-content: end;
    align-items: center;
    padding: 40px 40px 0 0;
}

.order {
    transform: scale(1.5);
    z-index: -1;
    margin-right: 40px;
}

